  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  font-family: Helvetica;
}

.container {
  grid-template: "nav nav nav" 100px
                 ". main ." 1fr
                 ". article ." 1fr
                 "footer footer footer" 200px
                 / 10vw 1fr 10vw;
  display: grid;
}

.nav_bar {
  background-color: #789;
  grid-area: nav;
}

